import React,{useState} from 'react';
import "../App.css";

function Nav() {
  let [idx,setIdx] = useState(0);
  let contents = [<Con1 />,<Con2 />];
  
  function tab(e){
    let num = e.target.dataset.num;
    let li = document.querySelectorAll('header a');

    li.forEach(function(el,k){
        if(num == k){
          el.classList.add('active');
        }else{
          el.classList.remove('active');
        }
    });
    setIdx(num);
  }
  return (
    <div className="navlist">
        <a onClick={tab} data-num="0">nav01</a>
        <a onClick={tab} data-num="1">nav02</a>
        <div>
          {contents[idx]}
        </div>
    </div>
  );
}
function Con1(){
  return( <div>component01</div> );
}
function Con2(){
  return( <div>component02</div> );
}

export default Nav;
