import React,{useState} from 'react';
import "../App.css";

function Time() {     
    let [time,set] = useState();
    let newDate = new Date();   
    newDate = newDate.getFullYear()+'-'+newDate.getMonth()+'-'+newDate.getDay();
          
    function printClock(){
        let newTime = new Date();
        newTime = newTime.getHours()+':'+newTime.getMinutes()+':'+newTime.getSeconds();
        set(newTime);
    }
    setTimeout(printClock,1000);
  
  return (
    <div className="timelist">
        Now...
        <span>
            {newDate}
        </span>
        <span>
            {time}
        </span>
    </div>
  );
}

export default Time;
