import React,{useState} from 'react';
import "../App.css";

function Img() {
    let data = [
        {src:'img/01.png' ,tit:'01', info:'01'},
        {src:'img/02.png' ,tit:'02', info:'02'},
        {src:'img/03.png' ,tit:'03', info:'선생님..'},
        {src:'img/04.png' ,tit:'04', info:'이거너무어려워요...,,'}
      ]
    
      let dataArr = [];
      data.forEach((d,k) => {
        dataArr.push(
          <div key={k}>
            <img src={d.src}></img>
            <h3>{d.tit}</h3>
            <p>{d.info}</p>
          </div>
        );
      });
    
      return(
        <article className="imglist">
          {dataArr}
        </article>
      );
}

export default Img;
